.map-swiper {
    position: absolute;
    top: 24px;
    left: 48px;
    z-index: 100;
    opacity: 1;
    transition: 0.3s ease;
    transition-delay: 1s ease;
    cursor: pointer;

    .swiper {
        position: relative;
        line-height: 24px;
        margin-bottom: 24px;
        color: #685F5F;
        transition: 0.3s ease;
        font-size: 18px;
        font-weight: bold;
        font-family: Helvetica-Bold;
        &:first-child {
            &:hover {
                color: #0064FA;
            }
        }
        
        &:last-child {
            &:hover {
                color: #E78ECE;
            }
        }

        &:first-child {
            &:active {
                color: #0064FA;
            }
        }
        &:last-child {
            &:active {
                color: #E78ECE;
            }
        }

        .marker {
            position: absolute;
            top: 3px;
            left: -36px;
            display: block;
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background: #191919;
            opacity: 0;
            transition: 0.3s ease;
        }

        .active {
            opacity: 1;
            left: -24px;
        }
    }
    .active {
        color: #191919;
    }
}

.hide-swiper {
    height: 0;
    opacity: 0;
    transition: 0.5s ease;
    overflow: hidden;
}