.s-fin {
    transition: 1s ease;
    .s-fin-list {
        position: absolute;
        top: calc(50% - 450px);
        left: calc(50% - 600px);
        z-index: 10;
        width: 1200px;
        height: 900px;

        .facility {
            position: absolute;
            width: 48px;
            height: 48px;
            cursor: pointer;

            .hit {
                position: absolute;
                top: 0;
                left: 0;
                z-index: 10;
                width: 100%;
                height: 100%;
                
                &:hover ~ .pin {
                    .pin-head {
                        overflow: hidden;
                        max-width: 300px;
                        border-radius: 9px;
                        transition: 1s ease;
                        .pin-point {
                            left: -12px;
                            opacity: 0;
                        }
                    }

                    .pin-name {
                        display: block;
                        width: max-content;
                        opacity: 1;
                        transition-delay: 0.2s;
                        font-family: HelveticaNeue-CondensedBold;
                    }
                }
            }

            .pin {
                transition: 0.3s ease;
                
                .pin-head {
                    position: absolute;
                    top: 10px;
                    left: 15px;
                    max-width: 18px;
                    height: 18px;
                    padding: 0 9px;
                    border-radius: 9px;
                    background: #685F5F;
                    transition: 0.5s ease;

                    .pin-point {
                        position: absolute;
                        top: 4px;
                        left: 4px;
                        width: 10px;
                        height: 10px;
                        background: #F0EBE0;
                        border-radius: 50%;
                        transition: 0.3s ease;
                    }
                }

                .pin-body {
                    position: absolute;
                    top: 27px;
                    left: 23px;
                    width: 2px;
                    height: 15px;
                    border-radius: 1px;
                    background: #685F5F;
                }

                .pin-name {
                    width: 0;
                    margin-top: 2px;
                    line-height: 14px;
                    font-size: 10px;
                    color: #F0EBE0;
                    opacity: 0;
                    transition: 0.3s;
                }

                &:hover {
                    width: 24px;
                    height: 24px;
                }
            }


            &:nth-child(1) {
                //premium area
                top: calc(34px);
                left: calc(714px);
            }

            &:nth-child(2) {
                //meditation garden
                top: calc(225px);
                left: calc(610px);
            }

            &:nth-child(3) {
                //play garden
                top: calc(400px);
                left: calc(490px);
            }

            &:nth-child(4) {
                // forest garden
                top: calc(370px);
                left: calc(610px);
            }

            &:nth-child(5) {
                // pet area
                top: calc(660px);
                left: calc(590px);
            }

            &:nth-child(6) {
                // organic garden
                top: calc(300PX);
                left: calc(740PX);
            }

            &:nth-child(7) {
                //main building
                top: calc(423px);
                left: calc(550PX);
            }

            &:nth-child(8) { 
                // bbq deck
                top: calc(370px);
                left: calc(540PX);
            }

            &:nth-child(9) { 
                // Sauna & Restaurant
                top: calc(170px);
                left: calc(740px);
            }

            &:nth-child(10) { 
                // Funicular
                top: calc(590px);
                left: calc(484px);
            }

            &:nth-child(11) { 
                // Centre of forest
                top: calc(410px);
                left: calc(810px);
            }

            &:nth-child(12) { 
                // sunset bridge
                top: calc(470px);
                left: calc(924px);
            }
        }

        .facility-selected {
            .pin {
                .pin-head {
                    overflow: hidden;
                    max-width: 600px;
                    border-radius: 9px 9px 9px 9px;
                    transition: 0.5s ease;
                    .pin-point {
                        left: -12px;
                        opacity: 0;
                    }
                }

                .pin-name {
                    display: block;
                    width: max-content;
                    opacity: 1;
                    transition-delay: 0.2s;
                    font-family: HelveticaNeue-CondensedBold;
                }
            }
        }
    }

    .bg-sono-fin {
        position: absolute;
        top: calc(50% - 450px);
        left : calc(50% - 600px);
        width: 1200px;
        opacity: 0.8;
    }
}